@font-face{font-family:"Inter";font-style:normal;font-weight:300;font-display:swap;src:local("Inter"),url("../../assets/fonts/Inter/Inter-Light.woff2") format("woff2")}@font-face{font-family:"Inter";font-style:normal;font-weight:500;font-display:swap;src:local("Inter"),url("../../assets/fonts/Inter/Inter-Medium.woff2") format("woff2")}@font-face{font-family:"Inter";font-style:normal;font-weight:700;font-display:swap;src:local("Inter"),url("../../assets/fonts/Inter/Inter-Bold.woff2") format("woff2")}@font-face{font-family:"Cormorant SC";font-style:normal;font-weight:500;font-display:swap;src:local("Cormorant SC"),url("../../assets/fonts/Cormorant_SC/CormorantSC-Medium.woff2") format("woff2")}@font-face{font-family:"Cormorant SC";font-style:normal;font-weight:600;font-display:swap;src:local("Cormorant SC"),url("../../assets/fonts/Cormorant_SC/CormorantSC-SemiBold.woff2") format("woff2")}@font-face{font-family:"Cormorant SC";font-style:normal;font-weight:700;font-display:swap;src:local("Cormorant SC"),url("../../assets/fonts/Cormorant_SC/CormorantSC-Bold.woff2") format("woff2")}.card-grid{display:grid;grid-template-columns:repeat(1, minmax(185px, 1fr));grid-gap:20px;list-style:none}.card-grid_dark .card-grid__item_drag{border-color:#dedede}.card-grid_dark .card-grid__drag-handle{background-color:#1a1818;color:#dedede}@media(min-width: 768px){.card-grid{grid-template-columns:repeat(2, minmax(220px, 1fr));grid-gap:24px}}@media(min-width: 1280px){.card-grid{grid-template-columns:repeat(3, minmax(260px, 1fr))}}.card-grid__item{position:relative;border:1px dashed rgba(0,0,0,0)}.card-grid__item:hover .card-grid__drag-handle{visibility:visible;opacity:1}.card-grid__item_drag{border-color:#575757;opacity:.5}.card-grid__drag-handle{position:absolute;cursor:grab;height:28px;width:28px;right:10px;top:10px;z-index:3;opacity:0;border-radius:4px;display:flex;justify-content:center;align-items:center;visibility:hidden;transition:all .3s ease-in;background-color:#fcfcfc;color:#575757}.card-grid__drag-handle::after{content:"⠿";font-size:20px;font-weight:300;color:inherit}