.sidebar {
  position: absolute;
  z-index: 100;
  right: 0;
  top: 0;
  height: 100vh;
  background-color: $color-secondary-white;
  box-shadow: 0 0 1000px 3000px rgba(3, 3, 3, 0.3);
  transform: translateX(100%);
  transition: all 0.15s ease-in;
  opacity: 0;

  &_dark {
    background-color: $color-primary-black;
  }
}
